.user-form {
  width: 480px;
}

.user-form > label {
  width: 100%;
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
}

.user-form input {
  margin-top: 10px;
  font: inherit;
  padding: 8px;
  outline: 1px solid black;
}

.user-form button {
  padding: 10px;
  border: 1px solid black;
}
